body{
  color: #334D6E;
  font-family: 'Poppins', sans-serif;
  height: auto;
}
.main_Apply{
  height: auto;
  position: relative;
  width: 100%;
  display: flex;
 
}
.logobox_Apply{
    width: 17%;
    height: 100vh;
    background-color: whitesmoke;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    box-shadow: 0 0 4px;
    padding: 0 2px;
  }

.logo{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  
}
.user_Apply{
  margin: 15px 1px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.user_Apply h3,.user_Apply p{
  margin: 0;
}
.list{
  display: flex;
}
.list li {
  margin:0 50px 0 10px;
    list-style: none;
  
   
  }
.list li a{
  text-decoration: none;
  color: #192A3E;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.list li a .fas,.list li a .far{
padding-right: 5px;
}
.NavContainer_Apply{
width: 83%;
height: auto;
position: absolute;
left: 17%;
}
.mdlndiv{
  margin: 2px 8px ;
}

.mdlndiv p, .mdlndiv h4{
  margin: 10px 0;
}
.cnct_p{
  text-align: center;
}
.VJDbtn{
  display: flex;
justify-content: center;
align-items: center;
}
.VJDbtn button{
outline: none;
margin: 10px auto;
display: flex;
justify-content: center;
align-items: center;
border: none;
background-color: #06be37;
border-radius: 6px;
padding: 8px;
font-size: 16px;
color: #fff;
cursor: pointer;
font-weight: bold;
}
.VJDbutton:hover{
  color: #06be37;
  background-color: #fff;
  border: 1px solid #06be37;
}

.Apply_Emailbtn{
  width: 160px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #008BDF;
  color: #fff;
  border: none;
  margin: 10px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}


.Apply_Supportbtn{
  width: 160px;
  cursor: pointer;
  background-color: #c4dceb;
  color: #192A3E;
  border: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 10px auto;
  padding: 8px;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;}
.navbar_Apply{
  height: 50px;
width: 82.5%;
position: fixed;
top: 0;
z-index: 1;
background-color: whitesmoke;
left: 17.4%;
display: flex;
box-shadow: 0 1px 4px grey;
background-color: white;
align-items: center;
justify-content: flex-end;
}
.whole{
  width: 100%;
  position: absolute;
  top: 46px;
}
.Admindiv{
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 25px;
  padding: 10px;
box-shadow: 0 1px 4px grey;
  background-color: #fff;
}
.Admindiv h2, .Admindiv p{
  margin: 0 10px;
}
.SRSSlist ol {
  display: flex;
  list-style: none;
}
.SRSSlist ol li{
  margin: 0 10px 0 40px;
 cursor: pointer;

}
.Applydiv{
  background-color: whitesmoke;
  margin:  20px 25px;
  padding: 10px;
  background-color: #fff;
  box-shadow: 0 1px 4px grey;
  width: 95%;
  height: auto;
}
.Applynow{
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba(128, 128, 128, 0.932);
}
.Applynow h2{
  margin: 0 10px;
}
.info_app{
  margin: 10px 30px;
}
.PDform{
  margin-bottom: 10px;
  border-bottom: 1px solid grey;
}
.PDform h3, .Social_prsnl_form h3{
  margin: 0 30px;
}

.Social_prsnl_form h3 span{
  font-size: 13px;
}
.SOCIAL .twtr,.SOCIAL .fb, .SOCIAL .lnkdn{
  font-size: 2.5rem;
  margin-left: 15px;
}
.ApplyForm, .prsnlForm{
  margin: 10px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Fname_Email, .Lname_Password, .Prtflio_github, .lnkd_other{
  display: flex;
  flex-direction: column;
}
.ApplyForm  input,.prsnlForm input{
  width: 300px;
  padding: 8px;
  border-radius: 10px;
  font-size: 14px;
  margin-bottom: 10px;
  border: 1px solid grey;
  outline: none;
}
.lftmail_Apply .fa-envelope, .lftphone_Apply .fa-phone-alt{
  padding-right: 8px;
  color: #192A3E;
}
.lftmail_Apply a{
  color: #192A3E;
  text-decoration: none;
}
.lftmail_Apply, .lftphone_Apply{
  display: flex;
  align-items: center;
  justify-content: center;
}
.lftphone_Apply{
  color: #192A3E;
  text-align: center;
  cursor: pointer;
}


.AGree, .Apply_rmbrme{
  display: flex;
  align-items: center;
  font-size: 14px;
}
.AGree span a{
  color: #008BDF;
  cursor: pointer;

  padding: 0 2px;
}
.Ck_boxes input{
  margin: 0 10px;
}
.Ck_boxes{
  font-weight: bold;
  margin: 10px 20px;
}
.bck_nxt_btns{
  display: flex;
  margin: 20px 30px;
  align-items: center;
  justify-content: space-between;
}
.bck_nxt_btns button{
  border: none;
  outline: none;
  color: #fff;
  background-color: #06be37;
  font-size: 16px;
  width: 90px;
  cursor: pointer;
  padding: 6px 10px;
  border-radius: 6px;
}
.Footpara{
  margin: 10px 30px ;
}
.Footpara a{
  color: #008BDF;
  cursor: pointer;
  text-decoration: none;
}


